'use client'

import Link from "next/link"
import { FormInput } from "../ui/FormInput/FormInput"
import { Button } from "../ui/Button/Button"
import { useState } from "react"
import { z } from "zod"
import { useForm } from "react-hook-form"
import { zodResolver } from "@hookform/resolvers/zod"

export const registerFormSchema = z.object({
    surname: z.string(),
    name: z.string(),
    email: z.string().min(1, 'Поле не может быть пустым').email(),
    password: z.string(),
    confirmPassword: z.string(),
}).superRefine(({confirmPassword, password}, ctx) => {
    if(confirmPassword !== password) {
        ctx.addIssue({
            code: 'custom',
            message: 'Пароли не совпадают',
            path: ['confirmPassword'],
        })
    }
})

export type RegisterFormSchema = z.infer<typeof registerFormSchema>

export const RegisterForm = () => {

    const {register, handleSubmit, formState: {errors}} = useForm<RegisterFormSchema>({
        resolver: zodResolver(registerFormSchema)
    })

    const handleSubmitRegister = (data: RegisterFormSchema) => {
        //
    }

    return (
        <form onSubmit={handleSubmit(handleSubmitRegister)} name="registerForm" className="flex flex-col gap-[16px] items-center justify-center border-1 border-sky-300 pt-12 pb-12 pl-10 pr-10 rounded-[20px] shadow-md">
            <h2 className="font-medium text-lg m-0">Регистрация</h2>
            <FormInput type={'text'} placeholder="Фамилия" label="Фамилия" {...register('surname')} errorMessage={errors.surname?.message}/>
            <FormInput type={'text'} placeholder="Имя" label="Имя" {...register('name')} errorMessage={errors.name?.message}/>
            <FormInput type={'email'} placeholder="E-mail" label="Email" {...register('email')} errorMessage={errors.email?.message}/>
            <FormInput type={'password'} placeholder="Пароль" label="Пароль" {...register('password')} errorMessage={errors.password?.message}/>
            <FormInput type={'password'} placeholder="Подтвердите пароль" label="Подтвердите пароль" {...register('confirmPassword')} errorMessage={errors.confirmPassword?.message}/>
            <Button classname={"font-medium bg-blue-500 pl-5 pr-5 pt-2 pb-2 rounded flex justify-center items-center text-white cursor-pointer"} type="submit">
                    Зарегистрироваться
                </Button>
                <Link href={'/auth'}>
                    <Button classname="cursor-pointer">
                        Войти
                    </Button>
                </Link>
        </form>
    )
}